import {useState,useEffect} from "react";
import reverseImg from '../img/reverse.png';
import kakao from '../img/kakao.jpeg';

export default function Contact({funAction}){
let [click, setClick] = useState(1);

useEffect(()=>{
    funAction();
})

// Card Rotate
function rotate(){
    const card = document.querySelector('.card-frame');
    setClick(click+1);
    let DEG = 180 * click
    card.style = `transform: rotateX(0deg) rotateY(${DEG}deg); transition: 1.5s;`;
}

// Card Rotate 3D
function rotate3D(e){
    const p = document.querySelector(".card-frame");
    let degX = e.clientX / window.innerWidth - 0.5;
    let degY = e.clientY / window.innerHeight - 0.5;
    if( click == 1){
        p.style = `transform:rotateX(${44 * degY}deg) rotateY(${-42 * degX}deg);transition: 0.2s;`;
    }
    else if ( click >= 2){
        p.style = `transform:rotateX(${44 * degY}deg) rotateY(${(180 * (click-1)) + (-42 * degX)}deg);transition: 0.2s;`;
    }
}

function mouseGone(){
    const p2 = document.querySelector(".card-frame");
    p2.style = `transform: rotateX(0deg) rotateY(${180*(click-1)}deg); transition: 1.5s;`;
}


return(
    <div className="profile">
        <div className="popup hide"></div>
        <div className="section sec-1">
            <div className="main-index">
                <div className="title2">
                    {/* <span className="main-title hide"></span> */}
                    <span className="main-title2">CONTACT.</span>
                </div>
            </div>
            <span className="go-btn next" id="aboutme">SCROLL</span>
        </div>
        <div className="section sec-2 contact" onMouseMove={rotate3D} onMouseOut={mouseGone}>
            <div className="card">
                <span className="main-title2 etc">Contact me!</span>
                <div className="card-frame">
                    <div className="card-front">
                        <div>
                            <p>PARK . <b>SEON</b> . HU</p>
                            <p>FRONTEND DEVELOPER</p>
                        </div>
                        <img className="kakao" src={kakao} alt=""/>
                        <ul>
                            <li><b>Mobile.</b>     010   6676   3460</li>
                            <li><b>E-mail.</b>     seonhu93@naver.com</li>
                            <li><b>Github.</b>     <a href="https://github.com/Seonhu-Park" target="_blank">github.com/Seonhu-Park</a></li>
                            <li><b>Notion.</b>     <a href="http://m.site.naver.com/0QBYs" target="_blank">seonhu.notion.site</a></li>
                        </ul>
                    </div>
                    <div className="card-back">
                        <div>dream <b>maker</b>,<br/>dream <b>coder</b>!</div>
                        <p>박 선 후 <b>.</b></p>
                    </div>
                </div>
                <img src={reverseImg} alt="" onClick={rotate}/>
            </div>
        </div>
    </div>
)
}